import qs from 'qs'
import {getCookie} from './cookie'
import {createRouter} from "../router"; //引用刚才我们创建的util.js文件，并使用getCookie方法
const router = createRouter()

window.BASE_URL = 'www.study.com';

let Config = {
  TIMEOUT: 500000,
  baseURL: {
    dev: '',
    prod: window.BASE_URL?window.BASE_URL:""
  }
};

// axios 配置

axios.defaults.baseURL = 'http://118.31.112.11'; //这是调用数据接口
axios.defaults.timeout = Config.TIMEOUT;
// axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';

let baseURL = '';
if(Config.baseURL.prod&&Config.baseURL.prod.indexOf('http')>=0){
  baseURL = Config.baseURL.prod;
}else{
  baseURL = Config.baseURL.dev;
}


