export function fetch (url, paramsData) {
  let token = '111'                // 获取用户的token
  return new Promise(function (resolve, reject) {
    axios({
      url: url,
      method: 'get',
      headers: {
        'Content-Type': 'application/json;charset=UTF-8',
        'Authorization': token
      },
      params: paramsData
    }).then(response => {
      resolve(response)
    }).catch(error => {
      reject(error)
    })
  })
}

// delete 请求
export function axiosDelete (url) {
  let token = '111'                // 获取用户的token
  return new Promise(function (resolve, reject) {
    axios({
      url: url,
      method: 'delete',
      headers: {
        'Content-Type': 'application/json;charset=UTF-8',
        'Authorization': token
      }
    }).then(response => {
      resolve(response)
    }).catch(error => {
      reject(error)
    })
  })
}

// put请求
export function put (url, data) {
  let token = '111'                 // 获取用户的token
  return new Promise(function (resolve, reject) {
    axios(url, {
      method: 'put',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': token
      },
      data: JSON.stringify(data)
    }).then(response => {
      resolve(response)
    }).catch(error => {
      reject(error)
    })
  })
}

// post请求
export function post (url, data) {
  const token = getCookie('session');              // 获取用户的token
  return new Promise(function (resolve, reject) {

    axios(url, {
      method: 'post',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
        'Accept':token
      },
      data: qs.stringify(data)
    }).then(response => {
      resolve(response)
    }).catch(error => {
      reject(error)
    })
  })
}

// post请求
export function postLogin (url, data) {
  return new Promise(function (resolve, reject) {
    axios(url, {
      method: 'post',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      },
      data: qs.stringify(data)
    }).then(response => {
      resolve(response)
    }).catch(error => {
      reject(error)
    })
  })
}

// patch请求
export function patch (url, data) {
  let token = '111'                // 获取用户的token
  return new Promise(function (resolve, reject) {
    axios(url, {
      method: 'patch',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': token
      },
      data: JSON.stringify(data)
    }).then(response => {
      resolve(response)
    }).catch(error => {
      resolve(error)
    })
  })
}



