// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import './global.css'
import ElementUI from 'element-ui';
import './assets/iconfont/iconfont.css';
import 'element-ui/lib/theme-chalk/index.css';
import {createRouter} from './router'
import {post,postLogin,fetch,patch,put} from './utils/https'
import {setCookie,delCookie,getCookie} from './utils/cookie'
Vue.prototype.$postLogin = postLogin
Vue.prototype.$post = post
Vue.prototype.$fetch = fetch
Vue.prototype.$setCookie = setCookie
const router = createRouter()



// 这个是请求页面路由的时候会验证token存不存在，不存在的话会到登录页
router.beforeEach((to, from, next) => {
  if(to.meta.requireAuth) {
    if (getCookie('session')) {  // 通过vuex state获取当前的token是否存在
      next();
    } else {
      next({
        path: '/',
        query: {redirect: to.fullPath}  // 将跳转的路由path作为参数，登录成功后跳转到该路由
      })
    }
  } else {
    next();
  }

});
// export default router;

Vue.use(ElementUI);
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



