const Login = resole=>require(['../components/Login.vue'],resole)
const Index = resole=>require(['../components/Index.vue'],resole)
const Preliminary = resole=>require(['../components/Preliminary.vue'],resole)
const Playoff = resole=>require(['../components/Playoff.vue'],resole)
const Register = resole=>require(['../components/register.vue'],resole)

const routers = [{
  path:'/',
  name:'login',
  component:Login,
  meta:{
    title:'login'
  }},
  {
  path:'/index',
  name:'index',
  component:Index,
  children:[
    {
      path:'',
      redirect:{name:'login'},
      meta:{requireAuth: true }
    },
    {
      path:'preliminary',
      name:'preliminary',
      component:Preliminary,
      meta:{requireAuth: true }
    },
    {
      path:'playoff',
      name:'playoff',
      component:Playoff,
      meta:{requireAuth: true }
    },
    {
      path:'register',
      name:'playoff',
      component:Register
    }
  ]
}]


export default routers



